@charset "utf-8"; 

/* 
@名称: base 
@功能: 重设浏览器默认样式 
*/ 

/* 边框和内边距都计算在width里面 */ 

*,*:before,*:after{
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
} 

/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */ 

html { 
color:black; 
background:white; 
} 

/* 内外边距通常让各个浏览器样式的表现位置不同 */ 

body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,hr,button,article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section { 
margin:0; 
padding:0; 
} 

/* 去掉各Table cell 的边距并让其边重合 */ 

table { 
border-collapse:collapse; 
border-spacing:0; 
} 

/* IE bug fixed: th 不继承 text-align*/ 

th { 
text-align:inherit; 
} 

/* 去除默认边框 */ 

fieldset,img { 
border:none; 
} 

/* ie6 7 8(q) bug 显示为行内表现 */ 

iframe { 
display:block; 
} 

/* 去掉 firefox 下此元素的边框 */ 

abbr,acronym { 
border:none; 
font-variant:normal; 
} 

/* 一致的 del 样式 */ 

del{ 
text-decoration:line-through; 
} 

address,caption,cite,code,dfn,em,th,var { 
font-style:normal; 
font-weight:500; 
} 

/* 去掉列表前的标识, li 会继承 */ 

ol,ul,dl { 
list-style:none; 
} 

/* 对齐是排版最重要的因素, 别让什么都居中 */ 

caption,th { 
text-align:left; 
} 

/* 统一上标和下标 */ 

sub, sup { 
font-size:75%; 
line-height:0; 
position:relative; 
vertical-align:baseline; 
} 

sup { 
top:-0.5em; 
} 

sub { 
bottom:-0.25em; 
} 

/* 默认不显示下划线，保持页面简洁 */ 

ins,a { 
text-decoration:none; 
} 

/* IE6,7焦点点状线去除 */ 

a:focus,*:focus { 
outline:none; 
} 

/*button 和 input 继承父级字体以及去除边框*/ 

input,button,textarea,select,optgroup,option{
font-family:inherit;
font-size:inherit;
font-style:inherit;
font-weight:inherit
} 

input,button{
overflow: visible;
vertical-align:middle;
outline:none
} 

/* 清除浮动 */ 

.clearfix:before,.clearfix:after { 
content:""; 
display:table; 
} 

.clear:after { 
clear:both; 
overflow:hidden; 
} 

.clear { 
zoom:1; /* for IE6 IE7 */ 
} 

.clear{ 
clear:both; 
display:block; 
overflow:hidden; 
height:0; 
line-height:0; 
font-size:0; 
} 

/* 设置内联, 减少浮动带来的bug */ 

.fl { 
float:left; 
display:inline; 
} 

.fr { 
float:right; 
display:inline; 
} 

/* 设置显示和隐藏, 通常用来与 JS 配合 */ 

.hide { 
display:none !important; 
visibility:hidden; 
} 

.show { 
display:block !important; 
} 

/*边距*/ 

.ml5{
	margin-left: 5px;
}

